




















































import {TableToolbar} from "@/components";
import HomeDialog from "./components/addDialog.vue";
import { Getter,Action,namespace } from "vuex-class";
const userModule=namespace("user")
import {Component,Vue} from "vue-property-decorator"
@Component({
  components:{
    TableToolbar,
    HomeDialog
  }
})
export default class User extends Vue{
  @userModule.Getter("userList") userList
  @userModule.Action("getUserAction") getUserAction

  formInline={
    user: "",
    region: ""
  }
  person={}
  isShow:boolean=false
  isOnline:boolean= true
  allUserData:any[]=[]
  userData:any[]=[]
  pagination:any={
    total: 10,
    pageSizes: [10, 20, 30],
    pageSize: 10,
    layout: "total,size,prev,pager,next,jumper",
    currentPage: 1,
    pageIndex: 1
  }

  created() {
    this.getUser();
  }
  handleClick(row) {}
  //子传参
  handleDialog(data) {
    this.isShow = false;
    if (data) {
      
    }
  }
  handleEdit() {
    alert(1);
  }
  handleDelete() {
    alert(2);
  }
  addUser() {
    this.isShow = true;
  }

  handleSizeChange(val) {
    this.pagination.pageSize = val;
    this.setPage();
  }
    
  handleCurrentChange(val) {
    this.pagination.pageIndex = val;
    this.setPage();
  }

  setPage() {
    this.userData = [];
    let size = this.pagination.pageSize;
    let max = this.pagination.pageIndex * size;
    let min = (this.pagination.pageIndex - 1) * size;
    this.pagination.total = this.allUserData.length;

    let k = 0;
    this.allUserData.forEach((elem, i) => {
        if (i >= min && i < max) {
          if (k % size == 0) {
          }
          this.userData[k] = elem;
          k++;
        }
      });
    }
  async getUser() {
    let d=await this.getUserAction()
    this.allUserData = this.userList;
  }
}
