










































interface RuleFormValue{
    name: string,
    email: string,
    region: string,
    sex: string
}
import {Component,Prop,Watch,Emit,Vue} from "vue-property-decorator"
@Component({
  components:{

  }
})
export default class AddDialog extends Vue{
  
  @Prop(Boolean)
  outerVisible!:boolean

  ruleForm:RuleFormValue= {
    name: "",
    email: "",
    region: "",
    sex: ""
  }
  rules:any= {
    name:[
      { required: true, message: "请输入用户名", trigger: "blur" },
      { min: 3, max: 5, message: "长度在 3 到 8 个字符", trigger: "blur" }
    ],
    email: [{ required: true, message: "请输入邮箱", trigger: "blur" }],
    region: [{ required: true, message: "请选择身份", trigger: "change" }],
    sex: [{ required: true, message: "请选择性别", trigger: "change" }]
  }

  @Emit('change-dialog')
  handleStatus() {}

  @Emit('change-dialog')
  submitForm(formName) {
    (this as any).$refs[formName].validate(async (valid:boolean) => {
    if (valid) {
      let d=await (this as any).$API.adminModel.addUser(this.ruleForm)
      if (d.code == 0) {
        return this.ruleForm
      } else {
            
      }
    } else {
      (this as any).$message({
        type:"error",
        message:"请填写完整信息"
      })
      return false;
    }
    });
    }
    resetForm(formName) {
      (this as any).$refs[formName].resetFields();
    }

    @Watch("outerVisible")
    onVisibleChange(newVal,oldVal){
      if (!newVal) {
        this.handleStatus();
      }
    }
}
